:root {
  height: 100%;
  overflow: hidden;

}

.App {
  font-size: 16px;
  text-align: center;
  background-color: #fff7ed;
  margin: 0em auto;
  height: 100%;
  min-height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.myLoader {
  height: 110%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
