body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
    overflow: scroll;
    overflow-x: hidden;
}
::-webkit-scrollbar {
    width: 0px;  /* remove scrollbar space */
    background: transparent;  /* optional: just make scrollbar invisible */
}
/* optional: show position indicator in red */
::-webkit-scrollbar-thumb {
    background: #FF0000;
}
:root {
  height: 100%;
  overflow: hidden;

}

.App {
  font-size: 16px;
  text-align: center;
  background-color: #fff7ed;
  margin: 0em auto;
  height: 100%;
  min-height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.myLoader {
  height: 110%;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.flex {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.flex-item {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: white;
  margin: 1em;
  width: 12em;
  height: 9em;
  border: 1px solid #ffa787;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  -webkit-transform: scale(1);
          transform: scale(1);
}

.flex-item:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.flex-item.grayscale:hover {
  -webkit-transform: scale(1);
          transform: scale(1)
}

.flex-image {
  width:99%;
}

.highlight {
  -webkit-transform: scale(1);
          transform: scale(1);
  border: 4px solid violet;
  box-shadow: 0.3em 0.3em 0.5em;
}

.grayscale {
  opacity: 0.75;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(179, 231, 255, 0.3);
}

.Modal {
  border: 2px solid rgb(120, 170, 200, 1);
  position: absolute;
  display: -webkit-flex;
  display: flex;
  padding: 1.5em;
  top: 20%;
  left: 20%;
  right: 20%;
  background-color: rgb(179, 231, 255, 1);
  opacity: 1;
}

.ReactModal__Overlay {
    opacity: 0;
    -webkit-transition: opacity 250ms ease-in-out;
    transition: opacity 250ms ease-in-out;
}

.ReactModal__Overlay--after-open{
    opacity: 1;
    -webkit-transition: opacity 250ms ease-in-out;
    transition: opacity 250ms ease-in-out;
}

.ReactModal__Overlay--before-close{
    opacity: 0;
    -webkit-transition: opacity 250ms ease-in-out;
    transition: opacity 250ms ease-in-out;
}

.flex {
  display: -webkit-flex;
  display: flex;
}

.center {
  -webkit-justify-content: center;
          justify-content: center;
}

.navBar {
  background-color: #fff5db !important;
  margin-bottom: 2em;
  border-bottom: 1px solid #ffa787
}

.small-margin {
  margin: 0.25em;
}

h1 {
  font-size: 3em;
}

.flex-icon {
  margin: 1em;
  margin-top: 0em;
  height: 3em;
  width: 3em;
  cursor: pointer;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  -webkit-filter: grayscale(100);
          filter: grayscale(100);
}

.flex-icon:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.flex-icon-focus {
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}

.margin-bottom {
  margin-bottom: 1em;
  color: red;
}

.depression {
  font-size: 2.5em;
  font-weight: bold;
  text-transform: capitalize;
  margin-bottom: 0.25em !important
}

.action-button {
  padding: 1.5em 0.5em;
  margin: 0.25em;
  border-radius: 2em;
  width: 18em;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 0.5em;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.action-button[disabled] {
  background-color: grey;
  border: 1px solid black;
}

